export default {
	data() {
		return {
			list: [],
			selectedList: [],
			isAllChecked: false,
			actId: '',
		}
	},
	onLoad(options) {
		// console.log(options)
		this.actId = options.id
	},
	onShow() {
		this.getList()
	},
	methods: {
		getList() {
			this.$http.get({
				url: '/v1/rest/manage/signInDetails',
				data: {
					activitiesId: this.actId
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					this.list = resp.data
				}
			})
		},
		handleCheck(item) {
			this.Item = item
			if(!item.signInState) {
				let selectedIndex = this.selectedList.indexOf(item.orderId)
				 if(selectedIndex >= 0) {
					this.selectedList.splice(selectedIndex, 1)
				}else{
					this.selectedList.push(item.orderId)
				}
				if(this.selectedList.length == this.list.length) {
					this.isAllChecked = true
				}else{
					this.isAllChecked = false
				}
			}
		},
		// 全选
		allCheck() {
			this.isAllChecked = !this.isAllChecked
			if(!this.isAllChecked) {
				this.selectedList = []
			}else{
				this.selectedList = []
				this.list.forEach(function(item){
					if(!item.signInState){
						this.selectedList.push(item.orderId)
					}
				},this)
			}
		},
		submit() {
			// console.log(typeof(this.selectedList))
			this.$http.get({
				url: '/v1/rest/manage/signInOk',
				data: {
					orderId: this.selectedList.join(',')
				}
			}).then(resp => {
				// console.log(resp)
				if(resp.status == 200) {
					this.getList()
					uni.showToast({
						title: '签到成功！',
						duration: 2000,
						icon: 'none'
					});
				}
			})
		}
	}
}
